.roomContainer {
	flex-direction: column;
	height: 80%;
	background-color: var(--bgColor);
	color: var(--textCol);
	padding: 10px;
}
.room {
	height: 80%;
	width: 100%;
	margin-top: 10px;
	max-width: 500px;
	margin-bottom: 15px;
	border-radius: 10px;
	padding: 10px 20px;

	background-image: linear-gradient(-45deg, var(--grad1), var(--grad2));
	box-shadow: -5px -5px 30px rgb(100, 100, 100, 0.5), 5px 5px 30px rgba(0, 0, 0, 0.8);
}
.roomHead {
	justify-content: space-around;
	margin: 10px auto;
	width: 80%;
	border-radius: 5px;
	padding: 5px;
	text-align: center;

	background-image: linear-gradient(-45deg, var(--grad1), var(--grad2));
	box-shadow: -5px -5px 30px rgb(100, 100, 100, 0.5), 5px 5px 30px rgba(0, 0, 0, 0.8);
}
.roomColor {
	width: 40px;
	height: 20px;
}
.roomHead h3 {
	color: var(--thisRed);
}
.roomType {
	padding: 5px;
	margin-bottom: 10px;
	letter-spacing: 2px;
}
.roomDevices {
	height: 65%;
	padding: 10px;
	border-radius: 10px;
	overflow-y: scroll;
}

@media screen and (max-height: 400px) {
	.room {
		height: 100%;
		width: 100%;
		max-width: 1000px;
	}
	.roomType {
		margin-bottom: 5px;
	}
	.roomDevices {
		height: 55%;
	}
}
