.popupMask {
	position: absolute;
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
	background-color: rgba(255, 255, 255, 0.2);
}
.popupCon {
	padding: 15px;
	width: 80%;
	max-width: 400px;
	background-color: white;
	box-shadow: 2px 2px 5px gray;
	border: 2px solid var(--myRed);
	border-radius: 20px;
}
.popupCon h3 {
	text-shadow: 5px 5px 5px rgb(150, 150, 150);
	letter-spacing: 10px;
	color: rgb(70, 70, 70);
}
.popupIcon svg {
	margin: 10px;
	width: 3rem;
	height: 3rem;
	color: rgb(70, 70, 70);
}
.popupNav {
	display: flex;
	justify-content: space-around;
}
.popupBtn {
	width: 80px;
	height: 40px;
	border: 1px solid var(--border);
	border-radius: 10px;
	background-color: rgba(255, 255, 255, 0.8);
	box-shadow: 0px 4px 4px var(--shadow);
	font-weight: bold;
	letter-spacing: 2px;
}
