.instContainer {
	width: 100%;
	height: 95%;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
}
.instTopSec {
	height: 10%;
	width: 100%;
}
.instCenSec {
	height: 60%;
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;

	background-color: rgba(255, 255, 255, 0.8);
	border: 1px solid #707070;
	border-radius: 10px;
	text-align: center;
	padding: 5px;
}
.instBotSec {
	height: 10%;
	width: 100%;
	display: flex;
	justify-content: space-around;
	align-items: center;
}
.instBtn {
	height: 40px;
	width: 100px;
	background-color: rgba(255, 255, 255, 0.8);
	box-shadow: 0px 4px 4px var(--shadow);
	border-radius: 10px;
	border: 1px solid var(--border);
	color: var(--text);
	letter-spacing: 2px;
	font-weight: bold;
	text-decoration: none;
}
.instContent {
	font-size: 0.8rem;
}
.instImg {
	width: 70%;
	max-width: 300px;
}
@media screen and (min-width: 750px) and (orientation: landscape) {
	.instContent {
		padding-left: 10px;
		font-size: 1.5rem;
	}
}

@media screen and (orientation: landscape) {
	.instContainer {
		display: flex;
		flex-direction: column;
		align-items: center;
	}
	.instCenSec {
		display: flex;
		flex-direction: row;
		align-items: center;
		width: 80%;
		height: 80%;
	}
	.instImg {
		transform: scale(0.8);
		width: 50%;
	}
	.instBtn {
		margin-top: 5px;
		width: 100px;
		height: 25px;
	}
}
