.activeContainer {
	width: 100%;
	height: 100%;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
}
.activeTopSec {
	width: 100%;
	height: 10%;
}
.activeCenSec {
	width: 100%;
	height: 60%;
}
.activeBotSec {
	flex-direction: column;
	width: 100%;
	height: 30%;
}

@media screen and (orientation: landscape) {
	.activeContainer {
		position: relative;
		display: flex;
		flex-direction: row;
	}
	.activeTopSec {
		width: 50%;
		top: 5px;
		left: 0;
		position: absolute;
	}
	.activeCenSec {
		order: 2;
		height: 100%;
	}
	.activeBotSec {
		flex-direction: column;
		width: 50%;
		height: 60%;
	}
}
