.numPad {
	height: 30%;
	width: 100%;
	max-width: 500px;
	display: flex;
	justify-content: space-evenly;
	align-items: center;
}
.pad {
	height: 30px;
	width: 30px;
	border: 1px solid rgb(70, 70, 70);
	border-radius: 50px;
	box-shadow: 0px 2px 5px rgb(109, 123, 138);
	background-color: rgba(232, 243, 255, 0.8);
}
@media screen and (orientation: landscape) and (min-width: 750px) {
	.pad {
		height: 50px;
		width: 50px;
	}
}
