.cellContainer {
	border: 1px solid rgb(70, 70, 70);
	background-color: rgba(255, 255, 255, 0.8);
	box-shadow: 0px 0px 3px 1px var(--shadow);
	display: grid;
	place-items: center;
}
.cellEmpty {
	font-weight: 900;
}
.cellFull {
	color: rgb(80, 80, 80);
}

.cellEmpty:hover {
	border: 3px solid var(--red);
	background-color: var(--cell);
}
.cellEmpty:focus {
	border: 3px solid var(--red);
	background-color: var(--cell);
}
