.weatherContainer { 
    height: 100vh;
    padding: 10px;
    background-color: rgb(241, 160, 53);
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}
.weatherContainer header {
    background: linear-gradient(-45deg, rgb(255, 255, 255, 0.2), rgb(0, 0, 0, 0.1));
    font-size: 0.8rem;
    letter-spacing: 5px;
    color: #ffffff;
    height: 10%;
    width: 100%;
    border-radius: 10px;
    flex-direction: column;
    box-shadow: 
        -5px -5px 10px rgb(255, 255, 255, 0.3),
        5px 5px 10px rgb(0, 0, 0, 0.3);     
}
.searchContainer {
    padding: 5px;
    width: 100%;
    flex-direction: column;
}
.searchInputs {
    width: 100%;
    display: flex;
    justify-content: space-around;
    margin-bottom: 15px;
}
.searchInputs input {
    background-color: rgb(241, 160, 53);
    border: 0px solid #303030;
    border-radius: 10px;
    width: 100%;
    height: 50px;
    font-size: 0.8rem;
    font-weight: 700;
    padding-left: 15px;
    color: rgb(255, 255, 255);
    box-shadow:
        inset -4px -4px 10px rgb(255, 255, 255, 0.3),
        inset 5px 5px 10px rgb(0, 0, 0, 0.3);
}
.searchInputs input:focus { outline: none; }
::placeholder { color: rgb(255, 255, 255); }
.searchContainer button {
    border: 0px solid black;
    border-radius: 5px;
    color: rgb(255, 68, 0);
    height: 30px;
    width: 100px;
    font-size: 1rem;
    font-weight: 800;
    background: linear-gradient(-45deg, rgb(255, 255, 255, 0.1), rgb(0, 0, 0, 0.1));
    box-shadow:
        -4px -4px 10px rgb(255, 255, 255, 0.3),
        5px 5px 10px rgb(0, 0, 0, 0.3);
}
.searchContainer button:focus { outline: none; }
.weatherCenSec { 
    border-radius: 10px;
    margin-bottom: 10px;
    height: 40%;
    width: 80%;
    background: linear-gradient(-45deg, rgb(255, 255, 255, 0.1), rgb(0, 0, 0, 0.1));
    box-shadow:
        -4px -4px 10px rgb(255, 255, 255, 0.3),
        5px 5px 10px rgb(0, 0, 0, 0.3);
}
.weatherData {
    height: 90%;
    width: 100%;
    font-size: 0.8rem;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    color: rgb(255, 255, 255);
    font-weight: 500;
}
.weatherData p { text-align: center; }
.weatherContainer footer {
    background-color: rgb(241, 160, 53);
    color: white;
    height: 10%;
    width: 100%;
    font-size: 0.8rem;
    justify-content: space-around;
    border-radius: 10px;
    background: linear-gradient(-45deg, rgb(255, 255, 255, 0.1), rgb(0, 0, 0, 0.1));
    box-shadow:
        -4px -4px 10px rgb(255, 255, 255, 0.3),
        5px 5px 10px rgb(0, 0, 0, 0.3);
}
.exitLink {
    border-radius: 5px;
    padding: 5px;
    width: 50px;
    text-decoration: none;
    text-align: center;
    color: white;
    background: linear-gradient(-45deg, rgb(255, 255, 255, 0.1), rgb(0, 0, 0, 0.1));
    box-shadow:
        -4px -4px 10px rgb(255, 255, 255, 0.3),
        5px 5px 10px rgb(0, 0, 0, 0.3);
}