.anim {
	position: relative;
	width: var(--width);
	height: var(--height);
}
.sq {
	width: 30%;
	height: 30%;
	background-color: rgba(255, 255, 255, 0.7);
	border: 1px solid rgba(112, 112, 112, 0.5);
	border-radius: 5px;
	opacity: var(--minOpa);

	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	align-content: space-around;
	justify-items: center;
}
.box {
	width: var(--boxWidth);
	height: var(--boxHeight);
	background-color: rgba(255, 255, 255, 0.7);
	border: 1px solid rgba(112, 112, 112, 0.5);
	border-radius: 2px;
}
.animDef {
	animation-duration: 3s;
	animation-delay: 0.5s;
	animation-iteration-count: 1;
	animation-fill-mode: forwards;
}

.sq0 {
	position: absolute;
	top: -100%;
	left: -100%;
	animation-name: displace0;
}
@keyframes displace0 {
	0% {
		top: -100%;
		left: -100%;
		transform: rotate(0deg);
		opacity: var(--minOpa);
	}
	100% {
		top: 0%;
		left: 0%;
		transform: rotate(360deg);
		opacity: var(--maxOpa);
	}
}
.sq1 {
	position: absolute;
	top: -100%;
	left: 35%;
	animation-name: displace1;
}
@keyframes displace1 {
	0% {
		top: -100%;
		left: 35%;
		transform: rotateX(0deg);
		opacity: var(--minOpa);
	}
	100% {
		top: 0%;
		left: 35%;
		transform: rotateX(360deg);
		opacity: var(--maxOpa);
	}
}
.sq2 {
	position: absolute;
	top: -100%;
	left: 200%;
	animation-name: displace2;
}
@keyframes displace2 {
	0% {
		top: -100%;
		left: 200%;
		transform: rotate(0deg);
		opacity: var(--minOpa);
	}
	100% {
		top: 0%;
		left: 70%;
		transform: rotate(-360deg);
		opacity: var(--maxOpa);
	}
}
.sq3 {
	position: absolute;
	top: 35%;
	left: -100%;
	animation-name: displace3;
}
@keyframes displace3 {
	0% {
		top: 35%;
		left: -100%;
		transform: rotateY(0deg);
		opacity: var(--minOpa);
	}
	100% {
		top: 35%;
		left: 0%;
		transform: rotateY(360deg);
		opacity: var(--maxOpa);
	}
}
.sq4 {
	position: absolute;
	top: 0%;
	left: 0%;
	width: 100%;
	height: 100%;
	animation-name: displace4;
}
@keyframes displace4 {
	0% {
		width: 100%;
		height: 100%;
		top: 0%;
		left: 0%;
		opacity: var(--minOpa);
	}
	100% {
		width: 30%;
		height: 30%;
		top: 35%;
		left: 35%;
		opacity: var(--maxOpa);
	}
}
.sq5 {
	position: absolute;
	top: 35%;
	left: 170%;
	animation-name: displace5;
}
@keyframes displace5 {
	0% {
		top: 35%;
		left: 170%;
		transform: rotateY(0deg);
		opacity: var(--minOpa);
	}
	100% {
		top: 35%;
		left: 70%;
		transform: rotateY(-360deg);
		opacity: var(--maxOpa);
	}
}
.sq6 {
	position: absolute;
	top: 170%;
	left: -100%;
	animation-name: displace6;
}
@keyframes displace6 {
	0% {
		top: 170%;
		left: -100%;
		transform: rotate(0deg);
		opacity: var(--minOpa);
	}
	100% {
		top: 70%;
		left: 0%;
		transform: rotate(360deg);
		opacity: var(--maxOpa);
	}
}
.sq7 {
	position: absolute;
	top: 170%;
	left: 35%;
	animation-name: displace7;
}
@keyframes displace7 {
	0% {
		top: 170%;
		left: 35%;
		transform: rotateX(0deg);
		opacity: var(--minOpa);
	}
	100% {
		top: 70%;
		left: 35%;
		transform: rotateX(-360deg);
		opacity: var(--maxOpa);
	}
}
.sq8 {
	position: absolute;
	top: 170%;
	left: 170%;
	animation-name: displace8;
}
@keyframes displace8 {
	0% {
		top: 170%;
		left: 170%;
		transform: rotate(0deg);
		opacity: var(--minOpa);
	}
	100% {
		top: 70%;
		left: 70%;
		transform: rotate(360deg);
		opacity: var(--maxOpa);
	}
}
