.smartAbouteContainer {
    height: 80%;
    background-color: var(--bgColor);
    color: var(--textCol);
    padding: 20px;
}
.smartAboute {
    height: 70%;
    width: 100%;
    max-width: 600px;
    padding: 5px;
    border-radius: 10px;
    flex-direction: column;
    justify-content: space-around;
    text-align: center;
    background-image: linear-gradient(45deg, var(--grad1), var(--grad2));
    box-shadow:
        -5px -5px 30px rgb(100, 100, 100, 0.5),
        5px 5px 30px rgba(0, 0, 0, 0.8);
}
.smartAboute h2 {
    color: var(--thisRed);
    padding: 10px;
    margin-bottom: 5px;
}
.smartAboute a { color: var(--thisRed); }