.smartHeader {
    height: 10%;
    min-height: 40px;
    background-color: var(--bgColor); 
    border-bottom: 2px solid var(--thisRed);
}
.smartHead { width: 70%; }
.smartHome { width: 15%; }
.smartMenu { width: 15%; }
.smartHead h1 { 
    color: var(--thisRed);
    font-size: 1.5em;
}
.headerLink { 
    color: var(--thisRed);
    border-radius: 10px;
    width: 50px;
    height: 50px;
    background-image: linear-gradient(-45deg, var(--grad1), var(--grad2));
    box-shadow:
        -5px -5px 30px rgb(100, 100, 100, 0.5),
        5px 5px 30px rgba(0, 0, 0, 0.8);
}

@media screen and (max-width: 400px), (max-height: 400px) {
    .headerLink {
        width: 35px;
        height: 35px;
    }
    .iconStyle {
        width: 1em;
        height: 1em;
    }
}