.navContainer {
	width: 100%;
	max-width: 500px;
	height: 70%;
	flex-direction: column;
}
.navTopSec {
	width: 100%;
	height: 50%;
}
.navBotSec {
	width: 100%;
	height: 50%;
	justify-content: space-between;
}
.navBotSec button,
.navTopSec button {
	color: var(--text);
	background-color: rgba(255, 255, 255, 0.8);
	border-radius: 10px;
	border: none;
	height: 40px;
	width: 250px;
	letter-spacing: 2px;
	font-weight: bold;
	border: 1px solid var(--border);
	box-shadow: 0px 4px 4px var(--shadow);
}
.navBotSec button:focus {
	outline: none;
}
.navTopSec button:focus {
	outline: none;
}
