.sudokuHomeContainer {
	width: 100%;
	height: 95%;
}
.sudokuHomeContainer h1 {
	font-family: Verdana, Geneva, Tahoma, sans-serif;
	text-shadow: 5px 5px 5px rgb(150, 150, 150);
	font-size: 2rem;
	letter-spacing: 10px;
	color: rgb(70, 70, 70);
}
.linkContainer {
	text-decoration: none;
	color: initial;
}
.animation {
	margin: auto;
	width: 100%;
	height: 100%;
	--minOpa: 0;
	--maxOpa: 0.9;
}
.openAnim {
	--width: 0px;
	--height: 0px;
	--boxWidth: 0px;
	--boxHeight: 0px;
	margin: auto;
	width: 100%;
	height: 100%;
}
.welcomeBot {
	height: 13%;
}

@media screen and (orientation: landscape) {
	.animation {
		transform: scale(0.5);
	}
}
