.smartAddroom {
	height: 80%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-around;
	background-color: var(--bgColor);
	color: var(--textCol);
}
.smartTypeSelect {
	width: 80%;
	max-width: 400px;
	height: 50px;
	border-radius: 10px;
	border: 0px;
	padding-left: 5px;
	background-color: var(--thisRed);
	color: var(--bgColor);
	box-shadow: -5px -5px 10px rgb(100, 100, 100, 0.5), 5px 5px 10px rgba(0, 0, 0, 0.8);
}
.smartTypeSelect:focus {
	outline: none;
}
.smartNameInput {
	width: 80%;
	max-width: 400px;
	height: 50px;
	border-radius: 10px;
	border: 0px;
	padding-left: 10px;
	background-color: var(--thisRed);
	box-shadow: -5px -5px 10px rgb(100, 100, 100, 0.5), 5px 5px 10px rgba(0, 0, 0, 0.8);
}
.smartNameInput:focus {
	outline: none;
}
.smartNameInput::placeholder {
	color: var(--bgColor);
}
.smartColorChooseContainer {
	width: 80%;
	max-width: 300px;
	display: flex;
	align-items: center;
	justify-content: space-around;
}
.smartColorChooseContainer label {
	font-size: 0.8rem;
}
.smartColorInput {
	width: 100px;
	height: 50px;
	border-radius: 5px;
	padding: 5px 10px;
	background-color: var(--thisRed);
	border: 0px;
	box-shadow: -5px -5px 10px rgb(100, 100, 100, 0.5), 5px 5px 10px rgba(0, 0, 0, 0.8);
}
.smartColorInput:focus {
	outline: none;
}
.addroomBtnLink {
	width: 100%;
}
.smartAddroom button {
	letter-spacing: 2px;
	width: 50%;
	max-width: 300px;
	height: 30px;
	border-radius: 10px;
	border: 0px;
	background-color: transparent;
	color: var(--thisRed);
	background-image: linear-gradient(-45deg, var(--grad1), var(--grad2));
	box-shadow: -5px -5px 30px rgb(100, 100, 100, 0.5), 5px 5px 30px rgba(0, 0, 0, 0.8);
}
.smartAddroom select:focus {
	outline: none;
}
.smartAddroom input:focus {
	outline: none;
}
.smartAddroom button:focus {
	outline: none;
}
