.levelContainer {
	width: 100%;
	height: 100%;
	flex-direction: column;
	justify-content: space-evenly;
}
.levelContainer .upperSec {
	width: 100%;
	height: 20%;
}
.levelContainer .upperSec h3 {
	font-size: 1.5rem;
	text-shadow: 5px 5px 5px rgb(150, 150, 150);
	letter-spacing: 10px;
	color: rgb(70, 70, 70);
}
.levelContainer .centerSec {
	width: 100%;
	height: 80%;
	flex-direction: column;
	justify-content: space-around;
}
.levBtn {
	color: var(--text);
	background-color: rgba(255, 255, 255, 0.8);
	border-radius: 10px;
	border: none;
	height: 40px;
	width: 250px;
	letter-spacing: 2px;
	font-weight: bold;
	border: 1px solid var(--border);
	box-shadow: 0px 4px 4px var(--shadow);
}
.levEasyBtn {
	border: 2px solid var(--myGrn);
}
.levEasyBtn:hover {
	background-color: var(--myGrnBG);
}
.levMedBtn {
	border: 2px solid var(--myYlo);
}
.levMedBtn:hover {
	background-color: var(--myYloBG);
}
.levHardBtn {
	border: 2px solid var(--myRed);
}
.levHardBtn:hover {
	background-color: var(--myRedBG);
}
.levBtn:focus {
	outline: none;
}
