.sudokuContainer {
	--text: rgb(70, 70, 70);
	--btnBack: rgb(245, 245, 245);
	--inpBack: rgb(245, 245, 245);
	--myRed: rgb(160, 60, 60);
	--myRedBG: rgb(243, 176, 176);
	--myYlo: rgb(204, 201, 0);
	--myYloBG: rgb(255, 255, 143);
	--myGrn: rgb(24, 107, 24);
	--myGrnBG: rgb(207, 247, 207);
	--border: rgb(70, 70, 70);
	--darkBorder: rgb(150, 150, 150);
	--shadow: rgb(168, 168, 168);
	--cell: rgb(220, 220, 220);

	background-color: rgb(0, 0, 0);
	padding: 10px;
	height: 100%;
}
.boarder1,
.boarder2,
.boarder3 {
	width: 100%;
	height: 100%;
}
.boarder1 {
	background-color: white;
	padding: 2px;
	border: 5px solid #0e2f4e;
	border-radius: 20px;
}
.boarder2 {
	padding: 2px;
	border: 5px solid #2578c5;
	border-radius: 15px;
}
.boarder3 {
	padding: 5px;
	border: 5px solid #6ab0f1;
	border-radius: 10px;
	background-image: url(components/images/notre-dame-blur.jpg);
	background-size: cover;
	background-repeat: no-repeat;
	background-size: 100% 100%;
}
.upperSec {
	text-align: center;
	height: 15%;
}
.centerSec {
	height: 70%;
}
.bottmSec {
	text-align: center;
	height: 13%;
}
.foot {
	height: 5%;
	margin-top: 5px;
	font-size: 0.8rem;
	display: flex;
	justify-content: space-around;
	align-items: center;
}
.foot .exitLink {
	border: 1px solid black;
	border-radius: 5px;
	padding: 2px;
	width: 50px;
	text-decoration: none;
	text-align: center;
	color: black;
}
