.smartDevice {
	display: flex;
	align-items: center;
	justify-content: space-around;
	border-radius: 30px;
	border: 10px solid var(--bgColor);
	width: 100%;
	padding: 5px;
	color: rgb(202, 202, 202);
	align-items: center;
	margin-bottom: 10px;
	background-image: linear-gradient(-45deg, var(--grad1), var(--grad2));
	box-shadow: -5px -5px 30px rgb(100, 100, 100, 0.5), 5px 5px 30px rgba(0, 0, 0, 0.8);
}
.smartDeviceHead {
	padding: 8px 10px;
	border-radius: 10px;
	box-shadow: inset -1px -1px 5px rgba(255, 255, 255), inset 1px 1px 5px rgba(0, 0, 0);
}
.smartDevice p {
	font-weight: 900;
}
.smartOnOffBtn {
	background-color: rgba(255, 255, 255, 0.8);
	color: var(--bgColor);
	border: 0px;
	border-radius: 5px;
	width: 30%;
	height: 30px;
	margin: 5px 0px;
	box-shadow: -1px -1px 5px rgb(182, 182, 182), 1px 1px 5px rgba(0, 0, 0);
}
.smartOnOffBtn:focus {
	outline: none;
}
.removeDeviceBtn {
	width: 25px;
	height: 25px;
	border: 0px;
	border-radius: 5px;
	background-color: transparent;
	color: rgb(202, 202, 202);
	box-shadow: -1px -1px 5px rgb(182, 182, 182), 1px 1px 5px rgba(0, 0, 0);
}
.removeDeviceBtn:focus {
	outline: none;
}

@media screen and (max-height: 400px) {
	.smartDevice {
		width: 160px;
		height: 30px;
		padding: 1px;
		flex-direction: row;
		justify-content: space-around;
		align-items: center;
	}
	.smartOnOffBtn {
		height: 28px;
		width: 54px;
		padding: 2px;
	}
}
