.addDevicesContainer {
	width: 100%;
	max-width: 500px;
	min-width: 300px;
	height: 70px;
	border-radius: 10px;
}
.addDeviceConatanier {
	display: flex;
	align-items: center;
	justify-content: space-around;
	width: 100%;
}
.addDeviceConatanier select,
.addDeviceConatanier button {
	border-radius: 10px;
	height: 40px;
	color: var(--bgColor);
	box-shadow: -5px -5px 30px rgb(100, 100, 100, 0.5), 5px 5px 30px rgba(0, 0, 0, 0.8);
}
.addDeviceConatanier select {
	border: 0px;
	width: 150px;
	padding-left: 5px;
	background-color: var(--thisRed);
}
.addDeviceConatanier select:focus {
	outline: none;
}
.addDeviceConatanier button {
	width: 50px;
	border: 0px;
	background-color: var(--bgColor);
	color: var(--thisRed);
	background-image: linear-gradient(-45deg, var(--grad1), var(--grad2));
}
.addDeviceConatanier button:focus {
	outline: none;
}
.addDeviceBtn {
	height: 50px;
	padding: 0px 20px;
	border-radius: 10px;
	border: 0px;
	font-weight: 900;
	font-size: 1rem;
	color: var(--thisRed);
	background-color: var(--bgColor);
	background-image: linear-gradient(-45deg, var(--grad1), var(--grad2));
	box-shadow: -5px -5px 30px rgb(100, 100, 100, 0.5), 5px 5px 30px rgba(0, 0, 0, 0.8);
}
.addDeviceBtn:focus {
	outline: none;
}

@media screen and (max-height: 400px) {
	.addDevicesContainer {
		width: 500px;
		height: 40px;
	}
	.addDeviceBtn {
		width: 40px;
		height: 40px;
	}
}
