.smartFooter {
	background-color: var(--bgColor);
	color: var(--textCol);
	height: 10%;
	border-top: 1px solid var(--thisRed);
	font-size: 0.6rem;
	display: flex;
	align-items: center;
	justify-content: space-around;
}
.smartFooter button {
	width: 70px;
	height: 25px;
	border-radius: 5px;
	border: 0px;
	background-color: var(--bgColor);
	color: var(--thisRed);
	background-image: linear-gradient(-45deg, var(--grad1), var(--grad2));
	box-shadow: -5px -5px 30px rgb(100, 100, 100, 0.5), 5px 5px 30px rgba(0, 0, 0, 0.8);
}
