.menuContainer {
	width: 100%;
	height: 95%;
	flex-direction: column;
	justify-content: space-evenly;
}
.menuTopSec {
	width: 100%;
	height: 20%;
	flex-direction: column;
	justify-content: space-around;
}
.menuTopSec {
	font-size: 1.5rem;
	text-shadow: 5px 5px 5px rgb(150, 150, 150);
	letter-spacing: 10px;
	color: rgb(70, 70, 70);
}
.menuCenSec {
	width: 100%;
	height: 80%;
	display: flex; /* Why flex dont work from centerIt ??? */
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
}
.menuBtn {
	height: 40px;
	width: 250px;
	background-color: rgba(255, 255, 255, 0.8);
	box-shadow: 0px 4px 4px var(--shadow);
	border-radius: 10px;
	border: 1px solid var(--border);
	color: var(--text);
	letter-spacing: 2px;
	font-weight: bold;
	text-decoration: none;
}
