.roomsm {
	width: 90%;
	height: 50px;
	padding: 5px;
	margin-bottom: 20px;
	border-radius: 5px;
	background-color: var(--bgColor);
	display: flex;
	box-shadow: -5px -5px 10px rgb(100, 100, 100, 0.5),
		5px 5px 10px rgba(0, 0, 0, 0.8);
}
.roomsmName {
	font-size: 1.5rem;
	color: var(--thisRed);
}
.roomsmColorContainer {
	width: 15%;
}
.roomsmColor {
	width: 25px;
	height: 25px;
	border-radius: 5px;
}
.roomsmLinkContainer {
	width: 70%;
	text-decoration: none;
}
.roomsmRemoveBtnContanier {
	width: 10%;
}
.roomsmRemoveBtn {
	width: 30px;
	height: 30px;
	border: 0px solid white;
	padding: 1px;
	border-radius: 5px;
	background-color: transparent;
	color: var(--thisRed);
	box-shadow: -5px -5px 20px rgb(100, 100, 100, 0.5),
		5px 5px 15px rgba(0, 0, 0, 0.8);
}
.roomsmRemoveBtn:focus {
	outline: none;
}

@media screen and (max-height: 400px) {
	.roomsm {
		height: 40px;
	}
	.roomsmName {
		font-size: 1.2rem;
	}
	.roomsmColor {
		width: 15px;
		height: 15px;
	}
	.roomsmRemoveBtn {
		width: 20px;
		height: 20px;
	}
}
