.smartHomepageContainer { 
    height: 80%; 
    flex-direction: column;
    text-align: center;
    background-color: var(--bgColor);
    color: var(--textCol);
}
.smartRoomsContainer {
    width: 100%;
    max-width: 800px;
    flex-direction: column;
    justify-content: flex-start;
    height: 80%;
    padding-top: 20px;
    overflow-x: scroll;
}
.btnCont {
    height: 15%;
    width: 100%;
}
.addRoomBtn {
    height: 50px;
    padding: 0px 20px;
    border-radius: 10px;
    border: 0px;
    font-weight: 900;
    font-size: 1rem;
    color: var(--thisRed);
    background-color: var(--bgColor);
    background-image: linear-gradient(-45deg, var(--grad1), var(--grad2));
    box-shadow:
        -5px -5px 30px rgb(100, 100, 100, 0.5),
        5px 5px 30px rgba(0, 0, 0, 0.8);
}
.addRoomBtn:focus { outline: none; }

@media screen and (max-height: 400px) {
    .smartRoomsContainer { height: 80%; }
    .addRoomBtn {
        width: 40px;
        height: 40px;
    }
    .btnCont { height: 20%; }
}